            let userInformation = JSON.parse(localStorage.getItem("userInformation"));
            // 좋아요한 사람들의 배열에 로그인한 닉네임이 포함되어있지 않은 경우 플러스
            if(!this.like.users.includes(this.$store.state.login.user.consumer_id)){
                axios.post('/api/likesPlus', {
